import { Daily } from './../../containers/daily/model/daily';
import { Injectable } from '@angular/core';
import { Constants } from '../constant';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../user/authentication-service.service';

@Injectable()
export class DailyService {
  constructor(private httpClient: HttpClient, private authenticationService: AuthenticationService) { }
  initDaily(daily) {
    return this.httpClient.post(Constants.POST_SAVE_DAILY_URL, daily);
  }
  getAllDaily() {
    return this.httpClient.get(Constants.GET_ALL_DAILY_URL);
  }
  verifyDayilySumary(date) {
    return this.httpClient.get(Constants.GET_VERIFY_SUMARY_CASH_URL + '/' + date);
  }
  confirmDayilySumary(daily) {
    return this.httpClient.put(Constants.GET_CONFIRM_SUMARY_CASH_URL, daily);
  }
  cancle(dailyId) {
    return this.httpClient.delete(Constants.GET_CANCLE_SUMARY_CASH_URL + '/' + dailyId);
  }
}



