import { Component, ViewChild, ElementRef, Input, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ProductService } from '../../../containers/product/service/product.service';
import { UnitService } from '../../../services/unit/unit.service';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';
import { StringUtils } from '../../../utils/string.utils';
import { NumberUtils } from '../../../utils/number.utils';
import { ToasterModule, ToasterService, ToasterConfig } from 'angular2-toaster/angular2-toaster';

const FIELD_DISABLE_IF_EDIT_PRICE = ["barcode", "name", "quantity", "unit", "category", "cost", "lowQuantity", "isCanSale"];
const FIELD_DISABLE_IF_EDIT = [];

@Component({
  selector: 'app-modal-add-product',
  providers: [
    ProductService, UnitService
  ],
  templateUrl: './modal-add-product.component.html',
  styleUrls: ['./modal-add-product.component.scss']
})

export class ModalAddProductComponent implements OnInit {
  @ViewChild('barcode') private elementRef: ElementRef;
  @Input() dataProduct: any;
  @Input() action: string;

  data: any = {};
  isDulicateBarcode: Boolean = false;
  categorys: any = [];
  public numberMask;
  public decimalMask;
  public units: any = [];

  constructor(
    public activeModal: NgbActiveModal,
    private productService: ProductService,
    private unitService: UnitService,
    private router: Router,
    public toasterService: ToasterService) { }

  ngOnInit() {
    if (this.dataProduct) {
      this.data = this.dataProduct;
    }
    this.productService.getAllCaterogys().subscribe((result) => {
      if (result) {
        this.categorys = result;
      }
    }, () => { });

    this.getActiveUnits();
    this.numberMask = createNumberMask({
      prefix: '',
      suffix: ''
    });

    this.decimalMask = createNumberMask({
      prefix: '',
      suffix: '',
      allowDecimal: true,
      allowNegative: true
    });
  }

  onScanBarcode(event) {
    if (event.target.value !== '') {
      this.data.barcode = StringUtils.convertLanguageIfNeed(event.target.value);
    }
  }

  compareTech(t1: any, t2: any): boolean {
    return t1 && t2 ? t1.name === t2.name : t1 === t2;
  }

  public ngAfterViewInit(): void {
    this.elementRef.nativeElement.focus();
  }

  save(productForm: NgForm) {
    if (this.data.id) {
      this.productService.updateOrders(this.setDateForUpdate(this.data))
        .subscribe(() => {
          this.onSuccess();
          this.activeModal.close(this.data);
        }, this.onFail);
    } else {
      this.productService.saveProduct(this.setDateForSave(this.data))
        .subscribe(() => {
          this.onSuccess();
          this.activeModal.close(this.data);
        }, this.onFail);
    }

  }

  validateDuplicateBarcode() {
    this.productService.getProductByBarcode(this.data.barcode)
      .subscribe((result) => {
        if (result) {
          this.isDulicateBarcode = true;
        } else {
          this.isDulicateBarcode = false;
        }
      }, () => {
        this.isDulicateBarcode = true;
      });
  }

  getProductByBarcode() {
    this.productService.getProductByBarcode(this.data.barcode)
      .subscribe((result) => {
        this.data = result;
      }, () => { });
  }

  getActiveUnits() {
    this.unitService.getActiveUnits()
      .subscribe((units) => {
        this.units = units;
      });
  }

  disableQty(): boolean {
    return this.action === "edit" ? true : false;
  }

  disableField(fieldName): any {
    let disable = null;
    if (this.action === "editPrice") {
      if (FIELD_DISABLE_IF_EDIT_PRICE.indexOf(fieldName) >= 0) {
        disable = '';
      }
    } else if (this.action === "edit") {
      if (FIELD_DISABLE_IF_EDIT.indexOf(fieldName) >= 0) {
        disable = '';
      }
    }

    return disable;
  }

  setDateForSave(result) {
    return {
      name: result.name,
      barcode: result.barcode,
      quantity: NumberUtils.repComar(result.quantity),
      categoryId: Number(result.category.id),
      imagePath: result.imagePath,
      cost: NumberUtils.repComar(result.cost),
      isCanSale: result.isCanSale == "true" ? true : false,
      salePrice: NumberUtils.repComar(result.salePrice),
      unit: result.unit,
      lowQuantity: NumberUtils.repComar(result.lowQuantity)
    };
  }

  setDateForUpdate(result) {
    return {
      id: result.id,
      name: result.name,
      barcode: result.barcode,
      quantity: NumberUtils.repComar(result.quantity),
      categoryId: result.categoryId,
      imagePath: result.imagePath,
      cost: NumberUtils.repComar(result.cost),
      isCanSale: (result.isCanSale === "true" || result.isCanSale === true) ? true : false,
      salePrice: NumberUtils.repComar(result.salePrice),
      unit: result.unit,
      lowQuantity: NumberUtils.repComar(result.lowQuantity)
    };
  }

  public toasterconfig: ToasterConfig =
    new ToasterConfig({
      tapToDismiss: true,
      timeout: 5000
    });

  onSuccess = () => {
    this.toasterService.pop('success', 'ทำรายการ', 'สำเร็จ');
  }

  onFail = () => {
    this.toasterService.pop('error', 'ทำรายการ', 'ไม่สำเร็จ');
  }
}




