import { Injectable } from '@angular/core';
import { Constants } from '../constant';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../user/authentication-service.service';
import { Member } from '../../models/member';

@Injectable()
export class MemberService {
  constructor(private httpClient: HttpClient, private authenticationService: AuthenticationService) { }
  saveMember(member) {
    return this.httpClient.post(Constants.SAVE_MEMBER_URL, member);
  }
  updateMember(member) {
    return this.httpClient.put(Constants.UPDATE_MEMBER_URL, member);
  }
  getAllMember() {
    return this.httpClient.get(Constants.GET_ALL_MEMBER_URL);
  }
  getMember(data) {
    return this.httpClient.get(Constants.GET_MEMBER_BY_FULL_NAME_OR_MOBILE_URL + '/' + data);
  }

  getMemberByCode(code) {
    return this.httpClient.get(Constants.GET_MEMBER_BY_CODE_URL + '/' + code);
  }
}



