import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { MemberService } from './../../../services/member/member.service';
import { Member } from '../../../models/member';
import { NgForm } from '@angular/forms';
import createNumberMask from 'text-mask-addons/dist/createNumberMask';

@Component({
  selector: 'app-modal-add-member',
  providers: [
    MemberService
  ],
  templateUrl: './modal-add-member.component.html',
  styleUrls: ['./modal-add-member.component.scss']
})
export class ModalAddMemberComponent implements OnInit {
  @Input() dataMember: any;
  member: any = {};
  public decimalMask;

  constructor(public activeModal: NgbActiveModal, private modalService: MemberService) {
    this.decimalMask = createNumberMask({
      prefix: '',
      suffix: '',
      allowDecimal: true,
      allowNegative: true
    });
  }

  ngOnInit() {
    if (this.dataMember) {
      this.member = this.dataMember;
    }
  }

  addMember(f: NgForm) {
    this.activeModal.close(this.member);
  }

}



