import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ProductService } from '../../../containers/product/service/product.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/map';

@Component({
  providers: [ProductService],
  selector: 'app-advance-search-product',
  templateUrl: './advance-search-product.component.html',
  styleUrls: ['./advance-search-product.component.scss']
})
export class AdvanceSearchProductComponent {
  searching = false;
  hideSearchingWhenUnsubscribed = new Observable(() => () => this.searching = false);

  constructor(public activeModal: NgbActiveModal, private productService: ProductService) { }

  searchAutoCompleteProduct(data: NgForm) {
    if (data.value.product !== '') {
      this.searching = true;
      this.productService.getProduct(data.value.product).subscribe((response) => {
        this.searching = false;
        this.activeModal.close(response[0]['name']);
      }, (error) => {
        this.searching = false;
      });
    }
  }

  search = (text$: Observable<string>) =>
    text$
      .debounceTime(300)
      .distinctUntilChanged()
      .do(() => this.searching = true)
      .switchMap(term =>
        this.productService.getProduct(term)
          .do((data) => {
            this.searching = false;
          })
          .catch(() => {
            this.searching = false;
            return Observable.of([]);
          }))
      .do(() => this.searching = false)
      .merge(this.hideSearchingWhenUnsubscribed)

  formatter = (x: { name: string }) => {
    if (x.name !== '') {
      this.activeModal.close(x.name);
    }
    return x.name;
  }

}



