import { Injectable } from '@angular/core';
import { Constants } from '../../../services/constant';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../../../services/user/authentication-service.service';
import { CategoryProductModel } from '../model/category-product-model';

@Injectable()
export class CategoryProductService {
  constructor(private httpClient: HttpClient, private authenticationService: AuthenticationService) { }
  getAllCaterogys() {
    return this.httpClient.get(Constants.GET_ALL_CATEGORY_URL);
  }
  saveCategoryProduct(data) {
    return this.httpClient.post(Constants.SAVE_CATEGORY_URL, data);
  }
  updateCategoryProduct(data) {
    return this
      .httpClient
      .put(Constants.UPDATE_CATEGORY_URL, data);
  }
  IsCanSale(saleable) {
    return saleable ? 1 : 0;
  }
  getSaleable(isCanSale) {
    return Number(isCanSale) === 1 ? true : false;
  }
}



