import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from '../../../services/user/user.service';
import { NgForm } from '@angular/forms';
import { UserInfo } from '../../../models/user';
@Component({
  selector: 'app-modal-add-user',
  providers: [UserService],
  templateUrl: './modal-add-user.component.html',
  styleUrls: ['./modal-add-user.component.scss']
})
export class ModalAddUserComponent implements OnInit {
  @Input() user: any = {};
  @Input() dataUser: any;
  isEditUser: boolean = false;
  public isDulicateUsername: boolean = false;
  constructor(public activeModal: NgbActiveModal, private userService: UserService) { }

  ngOnInit() {
    if (this.dataUser) {
      this.isEditUser = true;
      this.user = this.dataUser;
    }
  }

  addUser(f: NgForm) {
    if (this.isEditUser) {
      this.activeModal.close(this.user);
    } else {
      this.userService.getUserByUsername(this.user.username.toUpperCase())
        .subscribe((userInfo: UserInfo) => {
          if (userInfo && userInfo.username === this.user.username) {
            this.isDulicateUsername = true;
          } else {
            this.activeModal.close(this.user);
          }
        });
    }
  }

  validateExistingUsername() {
    if (this.user.username && !this.isEditUser) {
      this.userService.getUserByUsername(this.user.username.toUpperCase())
        .subscribe((userInfo: UserInfo) => {
          if (userInfo && userInfo.username === this.user.username) {
            this.isDulicateUsername = true;
          } else {
            this.isDulicateUsername = false;
          }
        }, () => {

        });
    }
  }

}



