import { Component, ViewChild, ElementRef, Input, OnInit} from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { CategoryProductService } from '../../../containers/category-product/service/category-product.service';
import { CategoryProductModel } from '../../../containers/category-product/model/category-product-model';

@Component({
  selector: 'app-modal-category-product',
  providers: [
    CategoryProductService
  ],
  templateUrl: './modal-category-product.component.html',
  styleUrls: ['./modal-category-product.component.scss']
})
export class ModalCategoryProductComponent implements OnInit{
  @ViewChild('name') private elementRef: ElementRef;
  @Input() action: string;
  @Input() categoryProduct: CategoryProductModel;

  data: CategoryProductModel;

  constructor(public activeModal: NgbActiveModal, private categoryProductService: CategoryProductService) {
    
  }

  ngOnInit() {
    if (this.action === "edit") {
      this.data = this.categoryProduct;
      this.data.isCanSale = this.categoryProductService.IsCanSale(this.categoryProduct.saleable);
    } else {
      this.data = new CategoryProductModel();
      this.data.saleable = true;
    }
  }

  submitCategoryProduct(categoryProductForm: NgForm) {
    this.activeModal.close(this.data);
  }

  public ngAfterViewInit(): void {
    this.elementRef.nativeElement.focus();
  }
}


