import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Constants } from '../constant';
import * as jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';
import { UserInfo } from '../../models/user';
@Injectable()
export class AuthenticationService {
  public token: string;
  public TOKEN_NAME: string;
  public USER_LOCAL_STOREAGE: string;
  constructor(private http: HttpClient, private router: Router) {
    this.TOKEN_NAME = 'currentUser';
    this.USER_LOCAL_STOREAGE = 'userProfile';
    const currentUser = JSON.parse(localStorage.getItem(this.TOKEN_NAME));
    this.token = currentUser && currentUser.token;
  }

  login(username: string, password: string) {
    this.http.post(Constants.LOGIN_URL, { username: username, password: password })
      .subscribe((response) => {
        if (response['token']) {
          this.token = response['token'];
          this.setToken(username, password, response);
          this.setUserInfoByUsernameInLocalStorage(username, response['token']);
          this.router.navigate(['/home']);
        }
      });
  }

  login2(username: string, password: string) {
    return this.http.post(Constants.LOGIN_URL, { username: username, password: password })
      .toPromise().then((response) => {
        if (response['token']) {
          this.token = response['token'];
          this.setToken(username, password, response);
          this.setUserInfoByUsernameInLocalStorage(username, response['token']);
          this.router.navigate(['/home']);
        }
      });
  }

  logout() {
    this.clearToken();
    this.router.navigate(['/page/login']);
  }

  clearToken() {
    localStorage.clear();
  }

  setUserInfoByUsernameInLocalStorage(username: string, token) {
    return this.http.get(Constants.USERS_BY_USERNAME_URL + username).subscribe((responseUser) => {
      localStorage.setItem(this.USER_LOCAL_STOREAGE, JSON.stringify(responseUser));
    });
  }

  getUserInfoInLocalStorage(): any {
    return JSON.parse(localStorage.getItem(this.USER_LOCAL_STOREAGE));
  }

  getToken(): string {
    return localStorage.getItem(this.TOKEN_NAME) || undefined;
  }

  setToken(username: string, password: string, response: any) {
    localStorage.setItem(this.TOKEN_NAME, JSON.stringify({ username: username, token: response['token'] }));
  }

  getTokenExpirationDate(token: string): Date {
    const decoded = jwt_decode(token);
    if (decoded.exp === undefined) {
      return null;
    }
    const date = new Date(0);
    date.setUTCSeconds(decoded.exp);
    return date;
  }

  isTokenExpired(): boolean {
    let result = false;

    if (this.getToken()) {
      result = true;
    }

    const date = this.getTokenExpirationDate(this.getToken());
    if (!(date.valueOf() > new Date().valueOf())) {
      result = true;
    }

    return result;
  }

  isHaveToken(): boolean {
    let result = false;

    if (this.getToken()) {
      result = true;
    }

    return result;
  }
}



