import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PreLoginLayoutComponent } from './components/template/pre-login/pre-login-layout.component';
import { AfterLoginComponent } from './components/template/after-login/after-login.component';
export const routes: Routes = [
  {
    path: '',
    component: AfterLoginComponent,
    data: {
      title: ''
    },
    children: [
      {
        path: '',
        loadChildren: './components/template/after-login/after-login.module#AfterLoginModule',
      }
    ]
  },
  {
    path: 'page',
    component: PreLoginLayoutComponent,
    data: {
      title: 'Pages'
    },
    children: [
      {
        path: '',
        loadChildren: './components/pages-plain/pages.module#PagesModule',
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }



