export class StringUtils {

    static keys = {
        "ๅ": "1",
        "/": "2",
        "_": "3",
        "-": "3",
        "ภ": "4",
        "ถ": "5",
        "ุ": "6",
        "ึ": "7",
        "ค": "8",
        "ต": "9",
        "จ": "0",
        "ข": "-",
        "ช": "=",
        "+": "1",
        "๑": "2",
        "๒": "3",
        "๓": "4",
        "๔": "5",
        "ู": "6",
        "฿": "7",
        "๕": "8",
        "๖": "9",
        "๗": "0",
        "๘": "-",
        "๙": "=",
        "!": "1",
        "@": "2",
        "#": "3",
        "$": "4",
        "%": "5",
        "^": "6",
        "&": "7",
        "*": "8",
        "(": "9",
        ")": "0",
    }
    static convertLanguageIfNeed(sorce): string {
        let result = "";
        if (sorce) {
            for (let i = 0, chr; i < sorce.length; i++) {
                if (this.keys[sorce.charAt(i)]) {
                    result += this.keys[sorce.charAt(i)]
                } else {
                    result += sorce.charAt(i);
                }
            }
        }
        return result;
    }


}


