import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import createNumberMask from 'text-mask-addons/dist/createNumberMask'


@Component({
  selector: 'app-add-expenditure',
  templateUrl: './add-expenditure.component.html',
  styleUrls: ['./add-expenditure.component.scss']
})
export class AddExpenditureComponent implements OnInit {
  @Input() expenditure: any = {};
  public decimalMask;
  
  constructor(public activeModal: NgbActiveModal) {
    this.decimalMask = createNumberMask({
      prefix: '',
      suffix: '',
      allowDecimal: true,
      allowNegative: true
    });
  }

  ngOnInit() {
  }

  addExpenditure(f: NgForm) {
    this.activeModal.close(this.expenditure);
  }

}



