import { NumberUtils } from './../../../utils/number.utils';
import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { DailyService } from './../../../services/daily/daily.service';
import { NgForm } from '@angular/forms';
import createNumberMask from 'text-mask-addons/dist/createNumberMask'
import { ToasterModule, ToasterService, ToasterConfig } from 'angular2-toaster/angular2-toaster';

@Component({
  selector: 'app-init-daily',
  providers: [
    DailyService
  ],
  templateUrl: './init-daily.component.html',
  styleUrls: ['./init-daily.component.scss']
})
export class ModalInitDailyComponent implements OnInit {
  data: any = {};
  public mask

  constructor(public activeModal: NgbActiveModal, private dailyService: DailyService, public toasterService: ToasterService) {
    this.mask = createNumberMask({
      prefix: '',
      suffix: ''
    });
  }

  ngOnInit() {
  }

  addDaily(f: NgForm) {
    this.dailyService.initDaily({
      startDayAmount: NumberUtils.toInt(this.data.startDayAmount),
      status: this.data.status,
      transactionType: this.data.transactionType,
      moneyType: this.data.moneyType,
      moneyFrom: this.data.moneyFrom,
      effectiveDate: this.data.effectiveDate
    })
      .subscribe(this.success, this.onError);
  }

  success = () => {
    this.activeModal.close();
  }

  onError = (res) => {
    let err = JSON.parse(res.error)
    if (err.code === "E101") {
      this.toasterService.pop('error', 'พบข้อผิดพลาด', 'ทำรายการซ้ำ');
    } else {
      this.toasterService.pop('error', 'พบข้อผิดพลาด', 'ไม่สำเร็จ');
    }
  }

  public toasterconfig: ToasterConfig =
    new ToasterConfig({
      tapToDismiss: true,
      timeout: 5000
    });



}



