import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

//3 party
import { ChartsModule } from 'ng2-charts/ng2-charts';

import { DataTableModule } from 'angular2-datatable';
import { AppComponent } from './app.component';
import { HeaderComponent } from './components/template/header/header.component';
import { AppRoutingModule } from './app.routing';
import { FooterComponent } from './components/template/footer/footer.component';
import { SidebarComponent } from './components/template/sidebar/sidebar.component';
import { BreadcrumbsComponent } from './components/template/breadcrumbs/breadcrumbs.component';
import { AsideComponent } from './components/template/aside/aside.component';
import { SidebarHeaderComponent } from './components/template/sidebar-header/sidebar-header.component';
import { SidebarFormComponent } from './components/template/sidebar-form/sidebar-form.component';
import { SidebarMinimizerComponent } from './components/template/sidebar-minimizer/sidebar-minimizer.component';
import { ModalAddProductComponent } from './components/product/modal-add-product/modal-add-product.component';
import { AuthenticationService } from './services/user/authentication-service.service';
import { UserService } from './services/user/user.service';
import { DismissComponent } from './components/shared/dismiss/dismiss.component';
import { AfterLoginComponent } from './components/template/after-login/after-login.component';
import { PreLoginLayoutComponent } from './components/template/pre-login/pre-login-layout.component';
import { NgxBarcodeModule } from 'ngx-barcode';
import { FocusDirective } from './components/shared/focus/focus.directive';
import { ModalAddMemberComponent } from './components/member/modal-add-member/modal-add-member.component';
import { ShowPaymentComponent } from './components/point-of-sale/modal/show-payment/show-payment.component';
import { ModalInitDailyComponent } from './components/daily/init-daily/init-daily.component';
import { LoaderService } from './services/loader.service';
import { AdvanceSearchProductComponent } from './components/point-of-sale/advance-search-product/advance-search-product.component';
import { ConfirmationComponent } from './components/shared/confirmation/confirmation.component';
import { ModalAddUserComponent } from './components/user/modal-add-user/modal-add-user.component';
import { AddExpenditureComponent } from './components/expenditure/add-expenditure/add-expenditure.component';
import { ModalCategoryProductComponent } from './components/category-product/modal-category-product/modal-category-product.component';
import { ModalAddQuotationComponent } from './components/quotation/modal-add-quotation/modal-add-quotation.component';
import { TokenInterceptor } from './token-interceptor';
// Angular 2 Input Mask
import { TextMaskModule } from 'angular2-text-mask';
import { ToasterModule, ToasterService } from 'angular2-toaster/angular2-toaster';


// Import directives
import {
  NAV_DROPDOWN_DIRECTIVES,
  SIDEBAR_TOGGLE_DIRECTIVES
} from './directives';

const APP_DIRECTIVES = [
  NAV_DROPDOWN_DIRECTIVES,
  SIDEBAR_TOGGLE_DIRECTIVES
];

@NgModule({
  declarations: [
    FocusDirective,
    AppComponent,
    HeaderComponent,
    FooterComponent,
    SidebarComponent,
    BreadcrumbsComponent,
    AsideComponent,
    SidebarHeaderComponent,
    SidebarFormComponent,
    SidebarMinimizerComponent,
    ModalAddProductComponent,
    DismissComponent,
    PreLoginLayoutComponent,
    AfterLoginComponent,
    PreLoginLayoutComponent,
    ModalAddMemberComponent,
    ShowPaymentComponent,
    ModalInitDailyComponent,
    AdvanceSearchProductComponent,
    ConfirmationComponent,
    ModalAddUserComponent,
    AddExpenditureComponent,
    ModalCategoryProductComponent,
    ModalAddQuotationComponent,
    ...APP_DIRECTIVES
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    DataTableModule,
    NgbModule.forRoot(),
    NgxBarcodeModule,
    ChartsModule,
    BrowserAnimationsModule,
    TextMaskModule,
    ToasterModule,
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true,
    },
    AuthenticationService,
    UserService,
    LoaderService],
  entryComponents: [
    ModalAddProductComponent,
    DismissComponent,
    ModalAddMemberComponent,
    ShowPaymentComponent,
    ModalInitDailyComponent,
    AdvanceSearchProductComponent,
    ConfirmationComponent,
    ModalAddUserComponent,
    AddExpenditureComponent,
    ModalCategoryProductComponent,
    ModalAddQuotationComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



