import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Headers } from '@angular/http';
import { AuthenticationService } from '../user/authentication-service.service';
import { Constants } from '../constant';
import { UserInfo } from '../../models/user';
@Injectable()
export class UserService {
  constructor(private http: HttpClient, private authenticationService: AuthenticationService) { }
  getUsers() {
    return this.http.get(Constants.USERS_URL);
  }
  getUserInfo(): UserInfo {
    return this.authenticationService.getUserInfoInLocalStorage();
  }
  saveUser(user) {
    return this.http.post(Constants.USERS_URL, user);
  }
  updateUser(user) {
    return this.http.put(Constants.UPDATE_USERS_URL, user);
  }
  getUserByUsername(username) {
    return this.http.get(Constants.USERS_URL + '/' + username);
  }
  resetPassword(user) {
    return this.http.patch(Constants.USERS_URL, user);
  }
}



