import { AuthenticationService } from './services/user/authentication-service.service';
import { Component, OnInit } from '@angular/core';
import { LoaderService } from './services/loader.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-root',
  template: `<router-outlet><span *ngIf="showLoader" class="loading"></span></router-outlet>`,
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  showLoader: boolean;
  title = 'app';
  constructor(
    private loaderService: LoaderService, private router: Router, private authenticationService: AuthenticationService) {
  }
  ngOnInit() {
    if (!this.authenticationService.isHaveToken()) {
      this.router.navigate(['/page/login']);
    } else {
      this.router.navigate(['/home']);
    }
    // this.loaderService.status.subscribe((val: boolean) => {
    //   this.showLoader = val;
    // });
  }
}



