export class NumberUtils {

    static toInt(numberText: string): Number {
        if (numberText) {
            numberText = numberText + "";
            return Number(numberText.replace(/,/g, ""));
        } else {
            return Number(0);
        }
    }

    static parseNumber(numberText: string): number {
        if (numberText) {
            return Number(numberText.replace(/,/g, ""));
        } else {
            return Number(0);
        }
    }

    static repComar(numberText: any): number {
        if (numberText) {
            let tmp = String(numberText);
            return Number(tmp.replace(/,/g, ""));
        } else {
            return Number(0);
        }
    }

    static toDecimalFormat(numberText: any): number {
        if (numberText) {
            numberText = numberText + "";
            let tmp = String(numberText);
            tmp = tmp.replace(/,/g, "")
            tmp = tmp.replace("%", "")
            tmp = tmp.replace(/ /g, "")
            return Number(tmp);
        } else {
            return Number(0);
        }
    }


}


